.hljs{display:block;overflow-x:auto;padding:.5em;background:#eee;-webkit-text-size-adjust:none}
.hljs-annotation,.hljs-decorator,.hljs-header{color:#007}
.hljs-attribute,.hljs-emphasis,.hljs-horizontal_rule,.hljs-link_url{color:#070}
.hljs-emphasis{font-style:italic}
.hljs-link_label,.hljs-string,.hljs-strong,.hljs-value,.scss .hljs-value .hljs-string{color:#d14}
.hljs-strong{font-weight:700}
.hljs-blockquote,.hljs-comment{color:#998;font-style:italic}
.asciidoc .hljs-title,.hljs-function .hljs-title{color:#900}
.hljs-class{color:#458}
.hljs-constant,.hljs-hexcolor,.hljs-id,.hljs-pseudo{color:teal}
.hljs-variable{color:#369}
.hljs-bullet,.hljs-javadoc{color:#970}
.hljs-doctype,.hljs-pi{color:#34b}
.hljs-code,.hljs-number{color:#099}
.hljs-important{color:red}
.hljs-label,.smartquote{color:#970}
.hljs-pragma,.hljs-preprocessor{color:#579}
.hljs-keyword,.hljs-reserved,.scss .hljs-value{color:#000}
.hljs-regexp{background-color:#fff0ff;color:#808}
.hljs-symbol{color:#990073}
.hljs-symbol .hljs-string{color:#a60}
.hljs-tag{color:#070}
.hljs-at_rule,.hljs-at_rule .hljs-keyword{color:#088}
.hljs-at_rule .hljs-preprocessor{color:#808}
.scss .hljs-attribute,.scss .hljs-tag{color:#339}